import React, { Component } from 'react';
import UserList from '../../components/UserList/UserList';
import { Grid, CssBaseline, Paper, Button, Typography } from '@material-ui/core';
import {v1 as uuid} from 'uuid';
import { withRouter } from 'react-router-dom';
import FileCopyIcon from '@material-ui/icons/FileCopy';

class Room extends Component {

    constructor(props){
        super(props);
        this.state = {
            roomInformation: {
                owner: 'joansweeney34',
                occupants: [
                {
                    'name': 'joan',
                    'username': 'joansweeney34',
                    'role': 'teacher'
                },
                {
                    'name': 'camilo',
                    'username': 'camilovb',
                    'role': 'student'
                },{
                    'name': 'tess',
                    'username': 'tess123',
                    'role': 'student'
                },{
                    'name': 'jean',
                    'username': 'jean123',
                    'role': 'student'
                },{
                    'name': 'tony',
                    'username': 'tony123',
                    'role': 'student'
                }]
            },
            myUsername: JSON.parse(localStorage.getItem('loggedUser')).username
        }
    }

    render() {

        const createGame = () => {
            const id = uuid();
            this.props.history.push(`/game/${id}`);
        }

        const copyToClipboard = () => {
            navigator.clipboard.writeText(window.location.href);
        };

        return (
            <div id="room"
                style={{
                    height: "100%",
                    minHeight: "100vh",
                    backgroundImage: 'url(https://source.unsplash.com/random)',
                    backgroundSize: 'cover',
                    backgroundPosition: 'center',
                    backgroundRepeat: 'no-repeat'
                }}
            >
                <Grid container component="main" xs={12} sm={6} md={3}>
                    <CssBaseline />
                    <Grid item xs={12} style={{padding: "10px 32px 10px 32px"}}>
                        <Paper
                            elevation={3}
                            style={{
                                margin: "40px auto 0px",
                                padding: "10px",
                                alignSelf: "baseline",
                                flex: "auto"
                            }}
                        >
                            <Typography 
                                variant="h6"
                                style={{marginTop: "5px"}}
                                gutterBottom>Room</Typography>
                            <UserList occupants={this.state.roomInformation.occupants} me={this.state.myUsername}/>
                            {(this.state.myUsername === this.state.roomInformation.owner)?
                            <React.Fragment>
                                <Button 
                                    variant="contained"
                                    color="secondary"
                                    style={{
                                        margin: "15px",
                                        width: "80%"
                                    }}
                                    onClick={createGame}
                                    >
                                    Start game
                                </Button>
                                <Button
                                    style={{
                                        marginTop: "0px",
                                        marginLeft: "15px",
                                        marginRight: "15px",
                                        width: "80%"
                                    }}
                                    >
                                    Start demo
                                </Button>
                            </React.Fragment>
                            : <React.Fragment />}
                        </Paper>
                    </Grid>
                    <Grid item xs={12}>
                        <Button
                            spacing={3}
                            color="primary"
                            variant="contained"
                            onClick={copyToClipboard}
                            style={{
                                marginLeft: "5px",
                                marginRight: "5px",
                                minWidth: "225px",
                                alignSelf: "baseline"
                            }}
                            startIcon={<FileCopyIcon />}
                        >
                            Copy link
                        </Button>
                    </Grid>
                </Grid>
                                
            </div>
        );
    }
}
export default withRouter(Room);